var io = require('socket.io-client');

var config = require('./config');

var events = require('events');

var store = {};

var initSocket = function initSocket(apiKey) {
  var uri = config.io.server + '/' + config.io.namespace.allFilings;
  var params = {
    query: {
      apiKey: apiKey
    }
  };
  store.socket = io(uri, params);
  store.socket.on('connect', function () {
    return console.log('Socket connected to', uri);
  });
  store.socket.on('filing', handleNewFiling);
  store.socket.on('filings', handleNewFilings);
  store.socket.on('error', console.error);
};

var handleNewFiling = function handleNewFiling(filing) {
  store.eventEmitter.emit('filing', filing);
};

var handleNewFilings = function handleNewFilings(filings) {
  store.eventEmitter.emit('filings', filings);
};

var close = function close() {
  if (store.socket.close) {
    store.socket.close();
  }
};

var connect = function connect(apiKey) {
  initSocket(apiKey);
  store.eventEmitter = new events.EventEmitter();
  return store.eventEmitter;
};

module.exports = connect;
module.exports.close = close;

if (require.main === module) {
  var apiKey = process.argv[2];
  var emitter = connect(apiKey);
  emitter.on('filing', function (filing) {
    return console.log(JSON.stringify(filing, null, 1));
  });
}